import React, { Component } from 'react';
import logo from './logo.svg';
import { CardList } from './components/card-list/card-list.component';
import { SearchField } from './components/inputs/search-field.components';
import './App.css';


class Clock extends Component {
  constructor(props) {
    super(props);
    this.state = { date: new Date() }
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState({
      date: new Date()
    });
  }

  render() {
    return (
      <div className="Clock">
        <h1>Hello, world!</h1>
        <h2>It is {this.state.date.toLocaleTimeString()}.</h2>
      </div>
    );
  }
}

class App extends Component {
  constructor() {
    super();
    this.state = {
      users: [],
      searchField: ''
    };
  }

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(response => response.json())
      .then(users => this.setState({ users: users }))
  }

  handleChange = (e) => {
    this.setState({ searchField: e.target.value })
  }

  render() {
    const { users, searchField } = this.state;
    const filteredUsers = users.filter(user => user.name.toLowerCase().includes(searchField.toLowerCase()));
    return (
      <div className="App App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <h1>Fuck the title</h1>
        <SearchField placeholder='search users' handleChange={this.handleChange} />
        <CardList users={filteredUsers} />
        <Clock />
      </div>
    )
  }
}


export default App;
